import React, { Component } from 'react'
import { detailProduct } from './data';
import axios from 'axios';

const ProductContext=React.createContext();

class ProductProvider extends Component {

  state={
    products:[],
    detailProduct:detailProduct,
    cart:[],
    modalOpen:false,
    modalProduct:detailProduct,
    cartSubTotal:0,
    cartTax:0,
    cartTotal:0,
    productsInState:[]
  };
  componentDidMount() {
    this.dataFromServer();
  }
  dataFromServer=()=>{
    axios.get('https://ecommerce-4895.herokuapp.com/business/')
        .then(json => {
            // console.log(json.data);
            this.setProducts(json.data);
            this.setState({productsInState:json.data})
        })
        .catch((err)=>{
            console.log(err);
        })
  }

  setProducts=(data)=>{
    let products=[];
    data.forEach(item=>{
        const singleItem={...item};
        products=[...products,singleItem];
    });
    this.setState({products})
  }

  update=()=>{
    this.dataFromServer();
  }
  getItem=(id)=>{
      const product=this.state.products.find(item => item.id===id);
      return product;
  }
  handleDetail=(id)=>{
    const product=this.getItem(id);
    this.setState(()=>{
      return {detailProduct:product}
    }) 
  }
  addToCart= id =>{
      let tempProducts=[...this.state.products];
      const index=tempProducts.indexOf(this.getItem(id));
      const product=tempProducts[index];
      product.inCart=true;
      product.count=1;
      const price=product.price;
      product.total=price;
      this.setState(
            ()=>{
            return {products:tempProducts,cart:[...this.state.cart,product]}
          },
          ()=>{
             this.addTotals();
          }
      )
  }

  openModal = id => {
    const product=this.getItem(id);
    this.setState(()=>{
      return  { modalProduct:product,modalOpen:true }
    })
  }
  closeModal=()=>{
    this.setState(()=>{
      return { modalOpen:false }
    })
  }
  increment = (id) =>{
      let tempCart=[...this.state.cart];
      let selectedProduct=tempCart.find(item => item.id === id);
      const index=tempCart.indexOf(selectedProduct);
      const product=tempCart[index];

      product.count=product.count+1;
      product.total=product.price*product.count;

      this.setState(()=>{
        return { cart:[...tempCart] }
      },()=>{
        this.addTotals();
      })
  }
  decrement = (id) =>{
      let tempCart=[...this.state.cart];
      let selectedProduct=tempCart.find(item => item.id === id);
      const index=tempCart.indexOf(selectedProduct);
      const product=tempCart[index];

      product.count=product.count-1;

      if(product.count === 0){
          this.removeItem(id);
      }
      else {
          product.total=product.price*product.count;
          this.setState(()=>{
            return { cart:[...tempCart] }
          },()=>{
            this.addTotals();
          })
      }
  } 
  removeItem=(id)=>{
    let tempProducts = [...this.state.products];
    let tempCart = [...this.state.cart];
    tempCart = tempCart.filter(item => item.id!==id);
    const index=tempProducts.indexOf(this.getItem(id));
    let removedProduct=tempProducts[index];
    removedProduct.inCart=false;
    removedProduct.count=0;
    removedProduct.total=0;

    this.setState(()=>{
      return {
        cart:[...tempCart],
        products:[...tempProducts]
      }
    },()=>{
      this.addTotals();
    })
  }
  clearCart=()=>{
    this.setState(()=>{
      return { cart:[] }
    },()=>{
      this.setProducts(this.state.productsInState);
      this.addTotals();
    })
  };
  addTotals=()=>{
    let subTotal=0;
    this.state.cart.map(item=>(subTotal += item.total));
    const tempTax=subTotal * 0.1;
    const tax=parseFloat(tempTax.toFixed(2));
    const total=subTotal+tax;
    this.setState(()=>{
      return {
        cartSubTotal:subTotal,
        cartTax:tax,
        cartTotal:total
      }
    })
  }
  reload=()=>{
    let condition=localStorage.getItem("reloadApp");
    if(condition==="yes"){
      this.dataFromServer();
      localStorage.removeItem("reloadApp");
    }
  }
  render() {
    this.reload();
    // this.dataFromServer();
    return (
      <ProductContext.Provider value={{
            ...this.state,
            update:this.update,
            handleDetail:this.handleDetail,
            addToCart:this.addToCart,
            openModal:this.openModal,
            closeModal:this.closeModal,
            increment:this.increment,
            decrement:this.decrement,
            removeItem:this.removeItem,
            clearCart:this.clearCart,
      }}>
            { this.props.children  }
      </ProductContext.Provider>
    )
  }
}

const ProductConsumer=ProductContext.Consumer;
export { ProductProvider,ProductConsumer   }
